<template>
    <div class="passwd_title">        
    </div>
    
    <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message">
                {{ this.message }}
    </div>

    <div class="container" v-if="!this.message" >
        <div class="row align-items-center">
            <div class="col-12 col-sm-8 col-md-6 col-lg-4 offset-0 offset-sm-2 offset-md-3 offset-lg-4">
                <Form @submit="this.onSendMail" :validation-schema="schema" v-slot="{ errors }">  
                    <div class="mb-2">
                        <label class="form-label" for="email">가입하신 이메일로 암호 재설정 하기</label>
                        <Field name="email" type="email" class="form-control" :class="{ 'is-invalid': errors.email }" />
                        <div class="invalid-feedback">{{errors.email}}</div>
                    </div>
                    <b-row>
                        <b-col cols="2"></b-col>
                        <b-col cols="4"><button type="submit" class="btn btn-warning">전 송</button></b-col>
                        <b-col cols="4"><button type="button"  class="btn btn-primary" @click="ongotoLogin">로그인</button></b-col>
                        <b-col cols="2"></b-col>
                    </b-row>
                    
                </Form>
            </div>            
        </div>        
    </div>    
</template>

<script>
import { Form, Field} from 'vee-validate';
import * as Yup from 'yup';
import router from "@/router";
// import {ref} from 'vue';

const schema = Yup.object().shape({
    email: Yup.string()
        .required('가입하신 이메일을 입력하세요')
        .email('이메일이 정확하지 않습니다'),    
});

export default{
    name: "PasswordChange",
    components:{
        Form,
        Field,
    },
    data() {
        return{
            schema:schema,
            message:"",
            timer: 300000,
            interval: "",            
        }        
    },    
    methods: {
        onSendMail(value) {
            
            console.log(value.email);

            // const formData = {
            //     email:value.email,              
            // };
            // this.$store.dispatch("sendResetPassword", formData);

            this.message="잠시만 기다리면 메일 발송합니다";
            console.log(this.message);

            this.startTimer();

            router.push("/home");
        },

        ongotoLogin(){
            router.push("/home");
        },

        startTimer() {
            
            
            console.log(this.timer);
                this.interval = setInterval(() => {
                    if (this.timer === 0) {
                        clearInterval(this.interval)
                        this.message=""; 
                        console.log('dddd',this.timer);               
                    } else {
                        this.timer--;
                        console.log('cccc',this.timer);
                    }             
                }, 1000000)
        },
    },
}

</script>

<style scoped>
.passwd_title {
    padding: 40px;
    text-align: center;
}
.passregion{
    padding: 40px;
}
</style>
